.hero {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  padding-top: 60px;
}

.hero-content {
  position: relative;
  z-index: 1;
  color: white;
  text-align: center;
}

.hero-content h1 {
  font-size: 4em;
}

.modes {
  position: relative;
  padding: 3rem 2rem;
  background: linear-gradient(145deg, #2a2a2a, #1a1a1a);
  text-align: center;
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  box-sizing: border-box;
  border-top: 5px solid #ff4500; 
  border-bottom: 5px solid #ff4500;
}

.background-left {
  position: absolute;
  top: 0;
  left: 0;
  width: 550px;
  height: 100%;
  background: url('../img/Left.jpg') no-repeat center center;
  background-size: cover;
  z-index: 0;
}

.background-right {
  position: absolute;
  top: 0;
  right: 0;
  width: 550px;
  height: 100%;
  background: url('../img/Right.jpg') no-repeat center center;
  background-size: cover;
  z-index: 0;
}

.modes h2 {
  font-size: 3em;
  margin-bottom: 1.5rem;
  color: #ff4500;
  text-transform: uppercase;
  z-index: 1;
}

.modes .mode {
  position: relative;
  text-align: left;
  max-width: 800px;
  background-color: #333333;
  padding: 1.5rem;
  border-radius: 15px; 
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.7);
  z-index: 1;
  margin: 0 auto 2rem auto;
}

.modes h3 {
  font-size: 2em;
  margin-bottom: 0.5rem;
  color: #ff4500;
}

.modes p {
  font-size: 1.2em;
  line-height: 1.8;
}

.championship {
  position: relative;
  padding: 3rem 2rem;
  background: linear-gradient(145deg, #2a2a2a, #1a1a1a);
  text-align: center;
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  box-sizing: border-box;
  border-top: 5px solid #ff4500;
  border-bottom: 5px solid #ff4500;
}

.championship h2 {
  font-size: 3em;
  margin-bottom: 1.5rem;
  color: #ff4500;
  text-transform: uppercase;
  z-index: 1;
}

.championship p {
  font-size: 1.2em;
  margin-bottom: 1rem;
  z-index: 1;
}

.video-container {
  display: flex;
  justify-content: center;
  margin-top: 1.5rem;
  z-index: 1;
}

.video-container video {
  max-width: 100%;
  width: 70%;
  border: 5px solid #ff4500;
  border-radius: 15px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.7);
  z-index: 1;
}

#bg-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
  overflow: hidden;
}